body{
    margin: 0;
    padding: 0;
    margin-top: 50px;
    background-color: antiquewhite;
    font-family: 'Raleway',sans-serif;
}
#root{
    width: 300px;
    margin: 0 auto;
    background-color: gray;
    border-radius: 20px;
    padding: 10px;

}

.flex-row {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.flex-col {
	display: flex;
    flex-direction: column;
    text-align: left;
}

.col-2 {
	width: 100%;
}

ul {
	list-style: none;
	padding: 0;
	width: 100%;
}

.results {
	background-color: #fff;
	width: 80%;
	color: black;
	height: 191px;
	justify-content: space-evenly;
	padding-left: 30px;
	padding-right: 30px;
	font-size: large;
}



/*INPUTS*/
.bill-total, 
.tip-percent, 
.tip-total {
	width: 48%;
	color: black;
	background-color: #8FD7CA;
	border-radius: 10px;
	margin-bottom: 10px;
}

.bill-total {
	justify-content: center;
	align-items: center;
	padding-top: 10px;
	padding-bottom: 10px;
	font-size: large;
}

.bill-total input {
	background-color: #8FD7CA;
	color: black;
	border:none;
	width: 60%;
	text-align: center;
	font-size: large;
}

.bill-total input:focus {
	outline: none;
} 

.dollar{
	text-align: center;
} 



.selected-tipPerc {
	background-color: #fff;
	color: black;
}

.tip-percent {
	width: 100%;
	justify-content: center;
	align-items: center;
	/*margin: 0;*/
}

.tip-percent ul {
	display: flex;
	width: 100%;
	height: 100%;
	margin: 0;
	border-radius: 10px;
}

.tip-percent ul li {
	height: 70px !important;
	cursor: pointer;
	width: 25% !important;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 10px;
}	

.tip-total {
	height: 70px;
	justify-content: center;
	text-align: center;
	font-size: large;
}


/*KEYPAD*/
.keypad li {
	width: 32%;
	cursor: pointer;
}
.keypad li button {
	width: 100%;
	height: 40px;
	background-color: #52BCAA;
	color: black;
	border: 1px solid #fff;
	cursor: pointer;
	margin: 5px 0;
	font-size: 1.5em;
}

 .keypad li button:focus {
	outline: none;
} 

.keypad li button:hover{
    background-color:white;
}


